<template>
  <div class="container">
    <div class="card bg-light my-3 mx-auto">
      <div class="card-header text-center">
        <h3>購物清單</h3>
      </div>

      <div class="card-body">
        <div class="container d-flex text-center">
          <div class="col-3">
            <h3>商品圖片</h3>
          </div>
          <div class="col-4">
            <h3>商品名稱</h3>
          </div>
          <div class="col-2">
            <h3>購買數量</h3>
          </div>
          <div class="col-2">
            <h3>金額</h3>
          </div>
          <div class="col-1">
            <h3>取消</h3>
          </div>
        </div>

        <hr />

        <div v-for="(item, index) in purchase_lists" :key="item.id">
          <div class="container d-flex text-center">
            <div class="col-3">
              <a :href="$data.product_particulars+item['id']">
                <img :src="item['img_main_thumbnail']" alt style="width: 40%" />
              </a>
            </div>

            <div class="col-4 d-flex">
              <div class="m-auto">
                <a :href="$data.product_particulars+item['products_id']">{{ item['product_name'] }}</a>
              </div>
            </div>

            <div class="col-2 d-flex">
              <div class="input-group m-auto">
                <div class="input-group-prepend">
                  <button
                    class="btn btn-outline-secondary p-1"
                    type="button"
                    id="decrease_btn"
                    v-on:click="decrease_quantity(index)"
                  >&#10134;</button>
                </div>
                <input
                  type="number"
                  class="form-control text-center"
                  aria-label="Example text with button addon"
                  aria-describedby="button-addon1"
                  :id="index"
                  :value="item['purchase_quantity']"
                />
                <div class="input-group-append">
                  <button
                    class="btn btn-outline-secondary p-1"
                    type="button"
                    id="increase_btn"
                    v-on:click="increase_quantity(index)"
                  >&#10133;</button>
                </div>
              </div>
            </div>

            <div class="col-2 d-flex">
              <div class="m-auto">$ {{ item['price'] * item['purchase_quantity'] }}</div>
            </div>

            <div class="col-1 d-flex">
              <div class="m-auto btn" v-on:click="removePurchaseProduct(item)">&#10006;</div>
            </div>
          </div>
          <hr />
        </div>
      </div>
      <div class="card-footer">
        <div class="btn">test</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["purchase_lists"],

  data: () => {
    return {
      product_particulars: "http://127.0.0.1:8000/product/"
    };
  },

  mounted() {},

  methods: {
    removePurchaseProduct: function(e) {
      // var vm = this;
      // console.log(vm.product_particulars);

      console.log("hit removePurchaseProduct");
      console.log(e);
      console.log(
        "http://127.0.0.1:8000/purchase_cart/" + e["purchase_carts_id"]
      );

      axios
        .delete("http://127.0.0.1:8000/purchase_cart/" + e["purchase_carts_id"])
        .then(res => {
          console.log(res);
        })
        .catch(err => console.error(err));
    },

    decrease_quantity: function(e) {
      console.log("join table id = " + e);
      console.log("purchase_carts_id = " + this.purchase_lists[e]["purchase_carts_id"]);
      // console.log(parseInt(this.purchase_lists[e]["purchase_quantity"]) - 5);

      var iValue = parseInt(document.getElementById(e).value);
      console.log(typeof iValue);
      console.log(parseInt(iValue));
      console.log(typeof parseInt(iValue));
      
      console.log("------------");
      console.log(typeof (iValue--));

      // document.getElementById(e).value =
      //   parseInt(this.purchase_lists[e]["purchase_quantity"]) - 5;
      document.getElementById(e).value = 
        this.purchase_lists[e]["purchase_quantity"] - 5;

      // console.log(typeof document.getElementById(e).value);
    },

    increase_quantity: function(e) {
      console.log(e);
      console.log(this.purchase_lists[e]["purchase_carts_id"]);
      console.log(parseInt(this.purchase_lists[e]["purchase_quantity"]) + 5);

      // document.getElementById(e).value =
      //   parseInt(this.purchase_lists[e]["purchase_quantity"]) + 5;
      document.getElementById(e).value =
        this.purchase_lists[e]["purchase_quantity"] + 5;
    }
  }
};
</script>

